<template>
  <div>
    <cms-breadcrumb to="/station/list" type='list'>
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-list-layout" v-loading="loading">
      <div class="view-warp">
        <p class="t-16">{{readData.title}}</p>
        <p>{{$moment(readData.createTime)}}</p>
        <hr class="hr">
        <p class="content">{{readData.content}}</p>
      </div>
    </div>
  </div>
</template>

<script>
import { storeMessageGet } from '@/api/index/index.js'
export default {
  data() {
    return {
      readData: {},
      messageId: ''
    }
  },
  watch: {
    $route: {
      handler(val, oldval) {
        if (val.query.id !== oldval.query.id) {
          this.read(val.query.id)
        }
      },
      // 深度观察监听
      deep: true
    }
  },
  methods: {
    read(id) { // 查看信息
      this.loading = true
      storeMessageGet({ messageId: id }).then(res => {
        if (res.code === 200) {
          this.readData = res.data
          this.loading = false
          this.$store.dispatch('setInfoData')
        }
      })
    }
  },
  created() {
    this.read(this.$route.query.id)
  }
}
</script>
<style>
  .view-warp{
    color: #666;
    max-width: 1440px;
    min-width: 600px;
    padding: 20px 180px;
    text-align: center;
    font-size: 14px;
  }
  .content{
    padding: 10px 50px;
  }
  .t-16{
    font-size: 16px;
    line-height: 36px;
  }
  .hr{
    border-top: none;
    border-bottom-color: #ccc;
    margin-top: 10px;
  }
</style>


